/* eslint-disable */
import React from "react";
import inputChecker from "./inputChecker";
import {ADD_ITEM} from "../../actions/messagesActions";
import {connect} from "react-redux";
import {withUtils} from "../HOCs/withUtils";
import {ReactComponent as IconCross} from "../../assets/cross.svg"
import {SET_PAGE} from "../../actions/pageContentActions";

const mapStateToProps = s => ({
	content: () => s.pageContent.feedbackSection,
	editMode: s.pageContent.editMode ? 'edit_mode pointer' : ''
});

const mapDispatchToProps = dispatch => ({
	ADD_ITEM: item => dispatch(ADD_ITEM(item)),
	SET_PAGE: (keys, initValue) => dispatch(SET_PAGE('feedbackSection', keys, initValue))
});

class FeedbackSection extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			name: "",
			phone: "",
			email: "",
			message: "",
			validated: {
				name: undefined,
				phone: undefined,
				email: undefined,
				message: undefined
			},
			file: '',
			files: [],
			loaded: 0
		};

		this.textRef = React.createRef();
		this.animated_elements = [
			this.textRef
		];
	}

	componentDidMount() {
		this.props.setRefs(this.animated_elements);
	}

	handleInputValue = (event) => {
		let {name, value} = event.currentTarget;
		this.setState({
			[name]: value
		});
	};

	handleFileInput = ({currentTarget}) => {
		let {files, value} = currentTarget;
		// Remove files over the limit
		files = [...files].splice(0, 10);

		// Delete files larger than 25 megabytes
		let invalidFileNames = [];
		files.forEach(({name, size}, i) => {
			if (size > 25000000) {
				invalidFileNames.push(name);
				delete files[i];
			}
		});
		files = files.filter(x => x);

		// Create alert with invalid file names
		if (invalidFileNames.length) {
			alert('–í—ã–±—Ä–∞–Ω–Ω—ã–µ —Ñ–∞–π–ª—ã –ø—Ä–µ–≤—ã—à–∞—é—Ç –ª–∏–º–∏—Ç –≤ 25 –º–µ–≥–∞–±–∞–π—Ç –∏ –Ω–µ –±—É–¥—É—Ç –ø—Ä–∏–∫—Ä–µ–ø–ª–µ–Ω—ã:\n'
				+ invalidFileNames.join('\n'))
		}

		this.setState({
			files,
			file: value,
			loaded: files.length
		})
	};

	clearFileInput = (e) => {
		e.preventDefault();
		this.setState({
			files: '',
			file: '',
			loaded: 0
		})
	};

	handleSubmit = (event) => {
		event.preventDefault();
		let {name, phone, email, message, files, loaded} = this.state;
		this.setState({
			validated: {
				name: inputChecker.name(name),
				phone: inputChecker.phone(phone),
				email: inputChecker.email(email),
				message: inputChecker.message(message)
			}
		}, async () => {
			for (let key in this.state.validated) {
				if (!this.state.validated[key]) return;
			}

			let body_form_data = new FormData();
			const config = {
				headers: {
					'content-type': 'multipart/form-data'
				}
			};

			let form = {
				date: new Date(),
				name,
				phone,
				email,
				message
			};

			body_form_data.append('form', JSON.stringify(form));

			if (loaded) files.forEach(file => body_form_data.append('files[]', file));

			try {
				let {status} = await this.props.ADD_ITEM({item: body_form_data, config});
				if (status === 200) {
					this.setState({
						name: "",
						phone: "",
						email: "",
						message: "",
						validated: {
							name: undefined,
							phone: undefined,
							email: undefined,
							message: undefined
						}
					}, () => alert("–°–æ–æ–±—â–µ–Ω–∏–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ!"));
				}
			} catch (e) {
				alert("–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ!");
				console.error(`FeedbackSection -> ADD_ITEM error: ${e.name}: ${e.message}`);
			}

		})

	};

	renderErrors = key => (
		this.props.content().form[key].errors.map((error, i) => {
			let {props: p} = this;
			return (
				<div className={`error error-${i + 1} ${i && 'mt-5'} ${p.editMode}`} key={`${key}-error-${i + 1}`}
				     onClick={() => p.editMode && p.SET_PAGE(`form['${key}'].errors['${i}']`, error)}>
					{error}
				</div>
			)
		})
	);

	showError = (validated) => {
		return validated === false ? 'd-block' : 'd-none';
	};

	render() {
		let {name, phone, email} = this.state.validated;
		let {loaded: files_loaded} = this.state;

		let {props: p} = this;
		let c = this.props.content();
		let {label_1, label_2, label_3, label_4} = c.form;
		return (
			<section id={'feedback-section'} className="centered py-50 px-15">

				<div className="content flex-column flex-align-center fade-up" ref={this.textRef}>

					<span className={`title ${p.editMode}`}
					      onClick={() => p.editMode && p.SET_PAGE('title', c.title)}>
						{c.title}
					</span>

					<div className={'hr-line w-50 mt-20 mb-10'}/>

					<form className="mt-15" onSubmit={this.handleSubmit}>
						<div className="input-group flex-space-between">
							<label className="label label-1 flex-column flex-align-center">
								<span className={`title mb-8 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('form.label_1.title', label_1.title)}>
									{label_1.title}
								</span>
								<input placeholder="–í–∞—à–µ –∏–º—è" value={this.state.name}
								       type="text" name="name" onChange={this.handleInputValue}/>
								<div
									className={`error-text mt-10 flex-column flex-align-center ${this.showError(name)}`}>
									{this.renderErrors('label_1')}
								</div>
							</label>
							<label className="label label-2 flex-column flex-align-center">
								<span className={`title mb-8 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('form.label_2.title', label_2.title)}>
									{label_2.title}
								</span>
								<input placeholder="+x (xxx) xxx-xx-xx" value={this.state.phone}
								       type="text" name="phone" onChange={this.handleInputValue}/>
								<div
									className={`error-text mt-10 flex-column flex-align-center ${this.showError(phone)}`}>
									{this.renderErrors('label_2')}
								</div>
							</label>
							<label className="label label-3 flex-column flex-align-center">
								<span className={`title mb-8 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('form.label_3.title', label_3.title)}>
									{label_3.title}
								</span>
								<input placeholder="–í–∞—à e-mail" value={this.state.email}
								       type="text" name="email" onChange={this.handleInputValue}/>
								<div
									className={`error-text mt-10 flex-column flex-align-center ${this.showError(email)}`}>
									{this.renderErrors('label_3')}
								</div>
							</label>
						</div>
						<label className="label label-4 flex-column flex-align-center">
							<span className={`title mb-8 ${p.editMode}`}
							      onClick={() => p.editMode && p.SET_PAGE('form.label_4.title', label_4.title)}>
								{label_4.title}
							</span>
							<textarea placeholder="–¢–µ–∫—Å—Ç —Å–æ–æ–±—â–µ–Ω–∏—è" value={this.state.message}
							          name="message" rows="10" onChange={this.handleInputValue}/>
							{/*<div className={`error-text flex-column flex-align-center`}></div>*/}
						</label>
						<div className="action-btns flex-center">
							<button className={`submit-btn centered pointer mx-10 px-18 py-10`} type="submit">
								<span className={`${p.editMode}`}
								      onClick={(e) => {
									      if (p.editMode) {
										      e.preventDefault();
										      p.SET_PAGE('submit_btn', c.submit_btn)
									      }
								      }}>
									{c.submit_btn}
								</span>
							</button>
							<label
								className={`upload-btn centered pointer ml-10 px-18 py-10 ${files_loaded && 'loaded'}`}>
								<span className={`${p.editMode}`}
								      onClick={(e) => {
									      if (p.editMode) {
										      e.preventDefault();
										      p.SET_PAGE('upload_btn', c.upload_btn)
									      }
								      }}>
									{files_loaded ? files_loaded : c.upload_btn}
								</span>
								<input className={'d-none'} type="file" name="file" multiple
								       value={this.state.file} onChange={this.handleFileInput}/>
							</label>
							<button onClick={this.clearFileInput}
							        className={`clear-btn centered pointer p-10 ${!files_loaded && 'd-none'}`}>
								<IconCross className="ml-auto" fill='#ffffff' width="20px" height="20px"/>
							</button>
						</div>
					</form>
				</div>

			</section>
		);
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(withUtils(FeedbackSection))