/* eslint-disable */
import React from 'react';
import BgImage from "../assets/background.jpg";
import {withUtils} from "./HOCs/withUtils";
import {connect} from "react-redux";
import {CHECK_AUTH} from "../actions/authActions";
import {SET_PAGE} from "../actions/pageContentActions";

const mapStateToProps = s => ({
	content: () => s.pageContent.homepageSection,
	editMode: s.pageContent.editMode ? 'edit_mode pointer' : ''
});

const mapDispatchToProps = dispatch => ({
	SET_PAGE: (keys, initValue) => dispatch(SET_PAGE('homepageSection', keys, initValue))
});

class Homepage extends React.Component {
	constructor(props) {
		super(props);

		this.state = {};
		this.bgRef = React.createRef();
	}

	componentDidMount() {
		this.props.setRefs(this.bgRef, true);
	}

	render() {
		let {props: p} = this;
		let c = this.props.content();

		return (
			<section id={'homepage-section'} className="centered px-15">

				<div className="background absolute" ref={this.bgRef} style={{backgroundImage: `url('${BgImage}')`}}>
					<div className="inner-background" />
				</div>

				<header className="relative flex-column flex-align-center">

					<span className={`title text-center mx-25 py-3 ${p.editMode}`}
					      onClick={() => p.editMode && p.SET_PAGE('title', c.title)}>
						{c.title}
					</span>
					<span className={`caption text-center mt-10 pt-2 pb-5 mx-25 ${p.editMode}`}
					      onClick={() => p.editMode && p.SET_PAGE('caption', c.caption)}>
						{c.caption}
					</span>
					<span className={`description text-center mt-10 ${p.editMode}`}
					      onClick={() => p.editMode && p.SET_PAGE('description', c.description)}>
						{c.description}
					</span>

				</header>

			</section>
		);
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(withUtils(Homepage));