/* eslint-disable */
import React, {useState, useRef, useEffect} from 'react';
import {Link} from "react-router-dom";
import {connect} from 'react-redux';
import {SWITCH_LANGUAGE} from "../actions/langActions";
import {INSERT_CONTENT} from "../actions/modalActions";
import modal_content from '../assets/documentation'

import ImgLogo from "../assets/logo.png";

import {ReactComponent as IconBurgerMenu} from "../assets/burger-menu.svg";
import {ReactComponent as IconAdmin} from "../assets/admin-with-cogwheels.svg";
import {ReactComponent as IconLocation} from "../assets/location.svg";
import {ReactComponent as IconPhone} from "../assets/phone.svg";
import {ReactComponent as IconCross} from "../assets/cross.svg";
import {TOGGLE_EDIT_MODE, UPDATE_PAGE} from "../actions/pageContentActions";

const mapStateToProps = ({lang, auth, pageContent}) => ({
	lang: lang.lang,
	loggedIn: auth.loggedIn,
	content: pageContent.navbar,
	editMode: pageContent.editMode
});

const mapDispatchToProps = dispatch => ({
	SWITCH_LANGUAGE: language => {
		dispatch(SWITCH_LANGUAGE(language))
	},
	INSERT_CONTENT: payload => dispatch(INSERT_CONTENT(payload)),
	TOGGLE_EDIT_MODE: boolean => dispatch(TOGGLE_EDIT_MODE(boolean)),
	UPDATE_PAGE: () => dispatch(UPDATE_PAGE())
});


function MobileNavbar(props) {

	const [menuHidden, setMenuHidden] = useState(true);
	const [dropdownList, setDropdownList] = useState(false);
	const [mountedEventFired, setMountedEventFired] = useState(false);
	const dropdownListRef = useRef(null);

	let p = props;

	let {content: c} = props;
	let hidden = menuHidden ? 'hidden' : '';

	let admin_btn_visibility = p.loggedIn ? {display: "block"} : {display: "none"};

	// TODO: –≤–∫–ª—é—á–∏—Ç—å –∫–Ω–æ–ø–∫–∏, –¥–æ–¥–µ–ª–∞—Ç—å –ø–µ—Ä–µ–≤–æ–¥—ã
	let langBtnText = p.lang === "RU" ? "EN" : "RU";

	// switchLanguage = () => {
	// 	let language = p.lang === "RU" ? "EN" : "RU";
	// 	p.SWITCH_LANGUAGE(language)
	// };

	useEffect(() => {
		document.body.addEventListener('click', handleOutsideClick, false);
		setMountedEventFired(true);
		return () => document.body.removeEventListener('click', handleOutsideClick, false);
	}, [mountedEventFired]);


	const scrollIntoView = (element_id) => {
		let element = document.querySelector(`#${element_id}`);
		let mobile_nav = document.querySelector('#page-mobile-navbar');
		let navHeight = document.querySelector('#page-navbar').offsetHeight;
		// –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –Ω–∞–ª–∏—á–∏–µ –º–æ–±–∏–ª—å–Ω–æ–π –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
		if (getComputedStyle(mobile_nav).display !== "none") {
			navHeight = mobile_nav.offsetHeight
		} else if (window.pageYOffset < 100) {
			// –û—Ç–Ω—è—Ç—å 10px –ø—Ä–∏ —Ç—Ä–∞–Ω—Å—Ñ–æ—Ä–º–∞—Ü–∏–∏ –±–ª–æ–∫–∞ –¥–µ—Å–∫—Ç–æ–ø –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
			navHeight -= 10;
		}

		let scroll_position = element.getBoundingClientRect().top + window.scrollY - navHeight;

		element.scrollIntoView({behavior: "smooth"});
		window.scroll({
			left: 0,
			top: scroll_position,
			behavior: 'smooth'
		})
	};

	const insertDataToModal = () => {
		setMenuHidden(true);
		scrollIntoView('feedback-section');
		p.INSERT_CONTENT(modal_content);
	};

	const linkAction = id => {
		setMenuHidden(true);
		scrollIntoView(id)
	};

	const handleOutsideClick = ({target}) => {
		if (!dropdownListRef.current.contains(target)) setDropdownList(false);
	};

	const adminPanel = () => (
		<div className={'admin-btn w-fit h-fit mx-7 relative pointer text-deselect'} style={admin_btn_visibility}
		     ref={dropdownListRef}>
			<IconAdmin className={"h-14 w-14"}
			           onClick={() => setDropdownList(!dropdownList)}/>

			<div className={`${!dropdownList && 'd-none'} dropdown-list flex-column`}>
				<Link className={'centered text-center p-5'} to='/admin'>
					–°–æ–æ–±—â–µ–Ω–∏—è
				</Link>
				<div className="hr h-2 mx-4"/>
				<button className={'edit-mode-btn centered text-center p-5 pointer'}
				        onClick={() => p.TOGGLE_EDIT_MODE(!p.editMode)}>
					–†–µ–∂–∏–º —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è
				</button>
				{p.editMode &&
				<>
					<div className="hr h-2 mx-4"/>
					<button className={'edit-mode-btn centered text-center p-5 pointer'}
					        onClick={() => p.UPDATE_PAGE()}>
						–°–æ—Ö—Ä–∞–Ω–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏—è
					</button>
				</>}
			</div>
		</div>
	);

	const renderMenu = () => (
		<div className={`menu centered fixed ${hidden}`}>
			<div className={'close-btn absolute pointer'} onClick={() => setMenuHidden(true)}>
				<IconCross className={'h-30 w-30'}/>
			</div>
			<ul className="menu-list flex-column flex-align-center">
				<li className="item item-1 my-10 pointer" onClick={() => linkAction('homepage-section')}>{c[0]}</li>
				<li className="item item-2 my-10 pointer" onClick={() => linkAction('about-section')}>{c[1]}</li>
				<li className="item item-3 my-10 pointer"
				    onClick={() => linkAction('word-directions-section')}>{c[2]}</li>
				<li className="item item-4 mx-15 pointer" onClick={insertDataToModal}>{c[3]}</li>
				<li className="item item-5 my-10 pointer" onClick={() => linkAction('feedback-section')}>{c[4]}</li>
				<li className="item item-6 mt-40 pt-15">
					<IconPhone className={'h-28 w-28 mx-7 pointer'} onClick={() => linkAction('info-section')}/>
					<IconLocation className={'h-28 w-28 mx-7 pointer'} onClick={() => linkAction('map-section')}/>
				</li>
			</ul>
		</div>
	);

	return (
		<nav id={'page-mobile-navbar'} className='fixed h-60 centered p-10'>
			<div className="item-1">
				<button className={'toggle-btn p-0 pt-2 w-fit h-fit pointer mr-auto'}
				        onClick={() => setMenuHidden(!menuHidden)}>
					<IconBurgerMenu className={"h-20 w-20"}/></button>
			</div>
			<div className={'item-2 logo flex-justify-center mx-auto'}>
				<img className={'h-30 pointer'} src={ImgLogo}
				     onClick={() => scrollIntoView('homepage-section')}/></div>
			<div className="item-3 icons flex-end flex-align-center ml-auto">
				<IconPhone className={'h-14 w-14 mx-7 pointer'} onClick={() => scrollIntoView('info-section')}/>
				<IconLocation className={'h-14 w-14 mx-7 pointer'} onClick={() => scrollIntoView('map-section')}/>
				{adminPanel()}
			</div>
			{renderMenu()}
		</nav>
	);
}

export default connect(mapStateToProps, mapDispatchToProps)(MobileNavbar);



