/* eslint-disable */
import React, {Component} from 'react';
import {Link} from "react-router-dom";
import {connect} from 'react-redux';
import {SWITCH_LANGUAGE} from "../actions/langActions";
import modal_content from '../assets/documentation'
import {INSERT_CONTENT} from "../actions/modalActions";
import {TOGGLE_EDIT_MODE, UPDATE_PAGE} from "../actions/pageContentActions";


import LogoDeltaP from "../assets/logo.png";
import {ReactComponent as IconPhone} from "../assets/phone.svg";
import {ReactComponent as IconLocation} from "../assets/location.svg";
import {ReactComponent as IconUser} from "../assets/user.svg";

const mapStateToProps = ({lang, auth, pageContent}) => ({
	lang: lang.lang,
	loggedIn: auth.loggedIn,
	content: pageContent.navbar,
	editMode: pageContent.editMode
});

const mapDispatchToProps = dispatch => ({
	SWITCH_LANGUAGE: language => {
		dispatch(SWITCH_LANGUAGE(language))
	},
	INSERT_CONTENT: payload => dispatch(INSERT_CONTENT(payload)),
	TOGGLE_EDIT_MODE: boolean => dispatch(TOGGLE_EDIT_MODE(boolean)),
	UPDATE_PAGE: () => dispatch(UPDATE_PAGE())
});

class Navbar extends Component {
	constructor(props) {
		super(props);

		this.state = {
			scrolled: false,
			dropdownList: false
		};

		this.dropdownListRef = React.createRef();
	}

	switchLanguage = () => {
		// let language = this.props.lang === "RU" ? "EN" : "RU";
		// this.props.SWITCH_LANGUAGE(language)
	};

	scrollIntoView = (element_id) => {
		if (window.location.pathname === '/') {
			let element = document.querySelector(`#${element_id}`);
			let mobile_nav = document.querySelector('#page-mobile-navbar');
			let navHeight = document.querySelector('#page-navbar').offsetHeight;
			// –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –Ω–∞–ª–∏—á–∏–µ –º–æ–±–∏–ª—å–Ω–æ–π –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
			if (getComputedStyle(mobile_nav).display !== "none") {
				navHeight = mobile_nav.offsetHeight
			} else if (window.pageYOffset < 100) {
				// –û—Ç–Ω—è—Ç—å 10px –ø—Ä–∏ —Ç—Ä–∞–Ω—Å—Ñ–æ—Ä–º–∞—Ü–∏–∏ –±–ª–æ–∫–∞ –¥–µ—Å–∫—Ç–æ–ø –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
				navHeight -= 10;
			}

			let scroll_position = element.getBoundingClientRect().top + window.scrollY - navHeight;

			element.scrollIntoView({behavior: "smooth"});
			window.scroll({
				left: 0,
				top: scroll_position,
				behavior: 'smooth'
			})
		}
	};

	checkYScroll = () => {
		let scroll_position = window.scrollY;
		if (scroll_position > 90) {
			this.setState({scrolled: true});
		} else if (scroll_position < 110) {
			this.setState({scrolled: false});
		}
	};


	componentDidMount() {
		document.addEventListener('scroll', this.checkYScroll, false);
		document.body.addEventListener('click', this.handleOutsideClick, false);
	};

	componentWillUnmount() {
		document.removeEventListener('scroll', this.checkYScroll);
		document.body.removeEventListener('click', this.handleOutsideClick, false);
	};

	insertDataToModal = (e) => {
		this.scrollIntoView('feedback-section')
		this.props.INSERT_CONTENT(modal_content)
	};

	handleOutsideClick = ({target}) => {
		if (!this.dropdownListRef.current.contains(target)) this.setState({dropdownList: false});
	};

	render() {
		let {content: c} = this.props,
			{props: p} = this,
			{state: s} = this;

		let langBtnText = p.lang === "RU" ? "EN" : "RU";

		// TODO: –≤–∫–ª—é—á–∏—Ç—å –∫–Ω–æ–ø–∫–∏, –¥–æ–¥–µ–ª–∞—Ç—å –ø–µ—Ä–µ–≤–æ–¥—ã
		let adminBtnVisibility = p.loggedIn ? {display: "list-item"} : {display: "none"};

		let scrolled = this.state.scrolled ? 'scrolled' : '';

		return (
			<nav id={'page-navbar'} className={`fixed px-15 ${scrolled}`}>
				<ul className="menu flex-end flex-align-center">
					<li className="item item-1 mr-auto">
						<img className={'main-logo'} src={LogoDeltaP}/>
					</li>
					<li className="item item-2 mx-15 pointer" onClick={() => {
						this.scrollIntoView('homepage-section')
					}}>{c[0]}</li>
					<li className="item item-3 mx-15 pointer" onClick={() => {
						this.scrollIntoView('about-section')
					}}>{c[1]}</li>
					<li className="item item-4 mx-15 pointer" onClick={() => {
						this.scrollIntoView('word-directions-section')
					}}>{c[2]}</li>
					<li className="item item-5 mx-15 pointer" onClick={this.insertDataToModal}>{c[3]}</li>
					<li className="item item-6 mx-15 pointer" onClick={() => {
						this.scrollIntoView('feedback-section')
					}}>{c[4]}</li>
					<li className="item item-7 mx-8 pointer" onClick={() => {
						this.scrollIntoView('info-section')
					}}>
						<IconPhone className={'h-14 w-14'}/>
					</li>
					<li className="item item-8 mx-8 pointer" onClick={() => {
						this.scrollIntoView('map-section')
					}}>
						<IconLocation className={'h-14 w-14'}/>
					</li>
					<li className="item item-9 mx-8 pointer relative text-deselect" style={adminBtnVisibility}
					    ref={this.dropdownListRef}>

						<IconUser className={'h-14 w-14'}
						          onClick={() => this.setState({dropdownList: !s.dropdownList})}/>

						<div className={`${s.dropdownList ? '' : 'd-none'} dropdown-list flex-column`}>
							<Link className={'centered text-center p-5'} to='/admin'>
								–°–æ–æ–±—â–µ–Ω–∏—è
							</Link>
							<div className="hr h-2 mx-4"/>
							<button className={'edit-mode-btn centered text-center p-5 pointer'}
							        onClick={() => p.TOGGLE_EDIT_MODE(!p.editMode)}>
								–†–µ–∂–∏–º —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è
							</button>
							{p.editMode &&
							<>
								<div className="hr h-2 mx-4"/>
								<button className={'edit-mode-btn centered text-center p-5 pointer'}
								        onClick={() => p.UPDATE_PAGE()}>
									–°–æ—Ö—Ä–∞–Ω–∏—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏—è
								</button>
							</>}
						</div>
					</li>
				</ul>
			</nav>
		);
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);



