/* eslint-disable */
import {
    INSERT_CONTENT,
    CLEAR_CONTENT
} from "../constants/actionTypes";

const initState = {
    title: null,
    content: null
};

export default ( state = initState, action ) => {
	switch ( action.type ) {
		case INSERT_CONTENT:
			return {
				...state,
                title: action.title,
                content: action.content
			};
		case CLEAR_CONTENT:
			return initState;
		default:
			return state;
	}
}
