/* eslint-disable */

import {
	GET_PAGE_CONTENT,
	SET_PAGE_CONTENT,
	SET_EDIT_MODE,
	UPDATE_PAGE_CONTENT,
	MAIN_PAGE_IS_LOADED
} from "../constants/actionTypes";

export const initState = {
	homepageSection: {},
	aboutSection: {},
	workDirectionsSection: {},
	infoSection: {},
	feedbackSection: {},
	navbar: {},
	footer: {},
	editMode: false,
	loaded: false,
	mainPageIsLoaded: new Promise(resolve => resolve(false))
};

export default ( state = initState, action ) => {

	switch ( action.type ) {
		case GET_PAGE_CONTENT:
			return {...initState, ...action.store, loaded: true};
		case UPDATE_PAGE_CONTENT:
			return {...state, ...action.store, loaded: true};
		case SET_PAGE_CONTENT:
			return {...state, editMode: false, ...action.newState};
		case SET_EDIT_MODE:
			return {...state, editMode: action.boolean};
		case MAIN_PAGE_IS_LOADED:
			return {...state, mainPageIsLoaded: action.promise};
		default:
			return state;
	}

}
