import React, {useEffect, useState} from 'react';
import Homepage from "./Homepage";
import AboutSection from "./AboutSection";
import WorkDirectionsSection from "./WorkDirectionsSection";
import Contacts from "./Contacts";
import {connect} from "react-redux";
import {CHECK_AUTH} from "../actions/authActions";
import {GET_PAGE, SET_MAIN_PAGE_IS_LOADED} from "../actions/pageContentActions";

const mapDispatchToProps = dispatch => ({
	SET_MAIN_PAGE_IS_LOADED: boolean => dispatch(SET_MAIN_PAGE_IS_LOADED(boolean))
});

function MainPage(props) {

	const [pageIsLoaded, setPageIsLoaded] = useState(false);

	useEffect(() => {
		setPageIsLoaded(true);
		props.SET_MAIN_PAGE_IS_LOADED(pageIsLoaded);
		return () => {
			setPageIsLoaded(false);
			props.SET_MAIN_PAGE_IS_LOADED(pageIsLoaded);
		};
	}, [pageIsLoaded]);


	return (
		<>
			<Homepage />
			<AboutSection />
			<WorkDirectionsSection />
			<Contacts />
		</>
	)
}

export default connect(null, mapDispatchToProps)(MainPage);