/* eslint-disable */
import React from 'react';
import {connect} from "react-redux";
import {withRouter} from "react-router";

const mapStateToProps = s => ({
	content: s.pageContent.footer,
	mainPageIsLoaded: s.pageContent.mainPageIsLoaded
});

function Footerbar(props) {

	const scrollIntoView = async (element_id) => {
		const action = () => {
			console.log(element_id)
			let element = document.querySelector(`#${element_id}`);
			let mobile_nav = document.querySelector('#page-mobile-navbar');
			let navHeight = document.querySelector('#page-navbar').offsetHeight;
			// –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –Ω–∞–ª–∏—á–∏–µ –º–æ–±–∏–ª—å–Ω–æ–π –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
			if (getComputedStyle(mobile_nav).display !== "none") {
				navHeight = mobile_nav.offsetHeight
			} else if (window.pageYOffset < 100) {
				// –û—Ç–Ω—è—Ç—å 10px –ø—Ä–∏ —Ç—Ä–∞–Ω—Å—Ñ–æ—Ä–º–∞—Ü–∏–∏ –±–ª–æ–∫–∞ –¥–µ—Å–∫—Ç–æ–ø –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
				navHeight -= 10;
			}

			let scroll_position = element.getBoundingClientRect().top + window.scrollY - navHeight;

			element.scrollIntoView({behavior: "smooth"});
			window.scroll({
				left: 0,
				top: scroll_position,
				behavior: 'smooth'
			})
		};

		if (props.location.pathname === '/') {
			action();
		} else {
			props.history.push('/');
			// Wait for promise show us that main page is loaded
			if (await props.mainPageIsLoaded) action();
		}

	};

	let {content: c} = props;
	return (
		<footer id={'footerbar'} className="centered py-40 px-15">

			<div className="content flex-space-between">

				<div className="copyright flex-column">
					<span className={'title'}>{c.title}</span>
					<span className={'mt-10 caption'}>¬© 2018 Faraks-01</span>
				</div>

				<ul className="menu flex-align-center mx-10">
					<li className="item item-1 mx-8 pointer" onClick={() => { scrollIntoView('homepage-section')} }>{c.menu[0]}</li>
					<li className="item item-2 mx-8 pointer" onClick={() => { scrollIntoView('about-section')} }>{c.menu[1]}</li>
					<li className="item item-3 mx-8 pointer" onClick={() => { scrollIntoView('word-directions-section')} }>{c.menu[2]}</li>
					<li className="item item-4 mx-8 pointer" onClick={() => { scrollIntoView('feedback-section')} }>{c.menu[3]}</li>
					<li className="item item-5 mx-8 pointer" onClick={() => { scrollIntoView('info-section')} }>{c.menu[4]}</li>
				</ul>

			</div>

		</footer>
	);
};

export default connect(mapStateToProps)(withRouter(Footerbar));
