import axios from 'axios'
import {
	GET_PAGE_CONTENT,
	MAIN_PAGE_IS_LOADED,
	SET_EDIT_MODE,
	SET_PAGE_CONTENT,
	UPDATE_PAGE_CONTENT
} from "../constants/actionTypes";

export const GET_PAGE = () => async dispatch => {
	try {
		let {data} = await axios.get('/api/pageContent');
		dispatch({
			type: GET_PAGE_CONTENT,
			store: data
		});
	} catch (e) {
		console.error(e)
	}
};

export const UPDATE_PAGE = () => async (dispatch, getState) => {
	try {
		let state = getState().pageContent;
		delete state.editMode;
		delete state.loaded;
		delete state.mainPageIsLoaded;

		let {status} = await axios.post('/api/pageContent', state);
		if (status === 200) {
			let {data} = await axios.get('/api/pageContent');
			dispatch({
				type: UPDATE_PAGE_CONTENT,
				store: data
			});
		}
	} catch (e) {
		console.error(e)
	}
};

export const SET_PAGE = (sectionName, keys, initValue) => async (dispatch, getState) => {

	let prompt = () => {
		let input = window.prompt('–í–≤–µ–¥–∏—Ç–µ –Ω–æ–≤–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ', '');
		return !input ? initValue : input;
	};

	let newState = getState().pageContent;
	eval(`newState[sectionName].${keys} = prompt()`);

	try {
		dispatch({
			type: SET_PAGE_CONTENT,
			newState
		});
	} catch (e) {
		console.error(e)
	}
};

export const TOGGLE_EDIT_MODE = boolean => dispatch => {
	try {
		dispatch({type: SET_EDIT_MODE, boolean});
	} catch (e) {
		console.error(e)
	}
};

export const SET_MAIN_PAGE_IS_LOADED = boolean => dispatch => {
	try {
		dispatch({type: MAIN_PAGE_IS_LOADED, promise: new Promise(resolve => resolve(boolean))});
	} catch (e) {
		console.error(e)
	}
};