/* eslint-disable */
import React from 'react';
import {withUtils} from "../HOCs/withUtils";

import IconWorldmap from "../../assets/worldmap.svg";
import {ReactComponent as IconClocks} from "../../assets/clocks.svg";
import {ReactComponent as IconSmartphone} from "../../assets/smartphone.svg";
import {ReactComponent as IconEnvelope} from "../../assets/envelope.svg";
import {connect} from "react-redux";
import {SET_PAGE} from "../../actions/pageContentActions";

const mapStateToProps = s => ({
	content: () => s.pageContent.infoSection,
	editMode: s.pageContent.editMode ? 'edit_mode pointer' : ''
});

const mapDispatchToProps = dispatch => ({
	SET_PAGE: (keys, initValue) => dispatch(SET_PAGE('infoSection', keys, initValue))
});

class InfoSection extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};

		this.textRef = React.createRef();
		this.animated_elements = [
			this.textRef
		];
	}

	componentDidMount() {
		this.props.setRefs(this.animated_elements);
	}

	render() {
		let {props: p} = this;
		let c = this.props.content();
		// onClick={() => p.editMode && p.SET_PAGE('title', c.title)}
		return (
			<section id={'info-section'} className="centered py-75 px-15">
					<div className="content flex-column flex-align-center fade-up" ref={this.textRef}>
						<span className={`title text-center ${p.editMode}`}
						      onClick={() => p.editMode && p.SET_PAGE('title', c.title)}>
							{c.title}
						</span>
						<div className={'hr-line w-50 mt-20 mb-10'} />
						<span className={`text mt-14 ${p.editMode}`}
						      onClick={() => p.editMode && p.SET_PAGE('text', c.text)}>
							{c.text}
						</span>
						<ul className="card-list mt-15">
							<li className="card card-1 flex-column flex-align-center">
								<div className="icon-wrapper p-14">
									<div className="worldmap-icon-wrapper w-82 h-82">
										<div className="img-group">
											<img className="w-142 h-79 icon-1" src={IconWorldmap} />
											<img className="w-142 h-79 icon-2" src={IconWorldmap} />
										</div>
									</div>
								</div>
								<span className={`title mt-13 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('cards[0].title', c.cards[0].title)}>
									{c.cards[0].title}
								</span>
								<hr className="my-15" />
								<span className={`text text-1 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('cards[0].text_1', c.cards[0].text_1)}>
									{c.cards[0].text_1}
								</span>
								<span className={`text text-2 mt-5 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('cards[0].text_2', c.cards[0].text_2)}>
									{c.cards[0].text_2}
								</span>
							</li>
							<li className="card card-2 flex-column flex-align-center">
								<div className="icon-wrapper w-80 h-80 p-14">
									<IconClocks />
								</div>
								<span className={`title mt-13 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('cards[1].title', c.cards[1].title)}>
									{c.cards[1].title}
								</span>
								<hr className="my-15" />
								<span className={`text text-1 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('cards[1].text_1', c.cards[1].text_1)}>
									{c.cards[1].text_1}
								</span>
								<span className={`text text-2 mt-5 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('cards[1].text_2', c.cards[1].text_2)}>
									{c.cards[1].text_2}
								</span>
							</li>
							<li className="card card-3 flex-column flex-align-center">
								<div className="icon-wrapper w-95 h-88 p-6">
									<IconSmartphone />
								</div>
								<span className={`title mt-13 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('cards[2].title', c.cards[2].title)}>
									{c.cards[2].title}
								</span>
								<hr className="my-15" />
								<span className={`text text-1 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('cards[2].text_1', c.cards[2].text_1)}>
									{c.cards[2].text_1}
								</span>
								<span className={`text text-2 mt-5 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('cards[2].text_2', c.cards[2].text_2)}>
									{c.cards[2].text_2}
								</span>
							</li>
							<li className="card card-4 flex-column flex-align-center">
								<div className="icon-wrapper w-64 h-90 p-20">
									<div>
										<IconEnvelope />
									</div>
								</div>
								<span className={`title mt-13 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('cards[3].title', c.cards[3].title)}>
									{c.cards[3].title}
								</span>
								<hr className="my-15" />
								<span className={`text text-1 ${p.editMode}`}
								      onClick={() => p.editMode && p.SET_PAGE('cards[3].text_1', c.cards[3].text_1)}>
									{c.cards[3].text_1}
								</span>
							</li>
						</ul>
					</div>
				</section>
		);
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(withUtils(InfoSection));