/* eslint-disable */
import React, { Component } from 'react';
import '../stylesheets/App.scss';
import { Switch, Route, withRouter } from 'react-router-dom'
import { connect } from 'react-redux';

import { CHECK_AUTH } from "../actions/authActions";

import Navbar from "./Navbar";
import Footerbar from "./Footerbar";
import Admin from "./Admin/index";
import MobileNavbar from "./MobileNavbar";
import MainPage from "./MainPage";
import ModalWindow from './ModalWindow';
import {GET_PAGE} from "../actions/pageContentActions";

const mapStateToProps = ({userAgent, pageContent}) => {
	return {
		browserName: userAgent.browserName,
		contentIsLoaded: pageContent.loaded
	}
};

const mapDispatchToProps = dispatch => {
	return {
		CHECK_AUTH: () => dispatch(CHECK_AUTH()),
		GET_PAGE: () => dispatch(GET_PAGE()),
	};
};

class App extends Component {

	componentWillMount = async () => {
		await this.props.GET_PAGE();
		await this.props.CHECK_AUTH();
	};

	render() {
		let { browserName, contentIsLoaded } = this.props;

		return contentIsLoaded ? (
			<div className="app flex-column h-inherit relative">
                <ModalWindow />
				<Route exact path="/" component={Navbar} />
				<Route exact path="/" component={MobileNavbar} />
				<main>
					<Switch>
						<Route exact path="/" component={MainPage} />
						<Route path="/admin" component={Admin}/>
					</Switch>
				</main>
				<Footerbar />
			</div>
		) : null
	};

};

export default withRouter(
	connect(mapStateToProps, mapDispatchToProps)(App)
)
