/* eslint-disable */
import React, {useReducer} from 'react';
import {ReactComponent as IconCross} from "../assets/cross.svg";
import { connect } from 'react-redux';

const mapStateToProps = state => {
	return {
		...state.modal 
	}
};

const mapDispatchToProps = dispatch => ({
		CLEAR_CONTENT: () => dispatch({type: 'CLEAR_CONTENT'})
	});

function ModalWindow({title, content, CLEAR_CONTENT}) {

    let trigger = title || content;

    return trigger && (
        <div className="modal-window-wrapper py-100 px-15 centered">
            <div className={'close-btn absolute pointer'} onClick={() => CLEAR_CONTENT()}>
                <IconCross fill='#ffffff' className={'h-30 w-30'} />
            </div>

            <div className="modal-window mx-auto flex-column">
                <span className="title text-center">{title}</span>
                <span className="text text-center mt-20">{content}</span>
            </div>
        </div>
    )
}

export default connect(mapStateToProps, mapDispatchToProps)(ModalWindow);