/* eslint-disable */
import React from 'react';
import {withUtils} from "./HOCs/withUtils";

import IconWaterDrop from "../assets/water-drop.svg";
import IconGears from "../assets/gears.svg";
import IconRecycle from "../assets/recycle.svg";
import IconFlask from "../assets/flask.svg";
import content from "../assets/work_directions.json"
import { connect } from 'react-redux';
import { INSERT_CONTENT } from "../actions/modalActions";
import {SET_PAGE} from "../actions/pageContentActions";

const mapStateToProps = s => ({
	content: () => s.pageContent.workDirectionsSection,
	editMode: s.pageContent.editMode ? 'edit_mode pointer' : ''
});

const mapDispatchToProps = dispatch => ({
    INSERT_CONTENT: (payload) => dispatch(INSERT_CONTENT(payload)),
	SET_PAGE: (keys, initValue) => dispatch(SET_PAGE('workDirectionsSection', keys, initValue))
});

class WorkDirectionsSection extends React.Component {
	constructor(props) {
		super(props);
		this.textRef1 = React.createRef();
		this.textRef2 = React.createRef();
		this.animated_elements = [
			this.textRef1,
			this.textRef2
		];
		this.state = {
			modal_payload: null
		}
	}

	componentDidMount() {
		this.props.setRefs(this.animated_elements);
	}

	setModalPayload = (payload = {}) => {
		this.props.INSERT_CONTENT(payload)
	}

	renderPart1List = () => {
		let icons = [
			IconWaterDrop,
			IconGears,
			IconRecycle,
			IconFlask
		];

		let {props: p} = this;

		return p.content().part_1.cards.map((c, i) => (
			<li className={`card card-${i + 1} flex-column flex-align-center`} key={`card-${i + 1}`}>
				<img src={icons[i]} className="icon w-66 h-66"/>
				<span className={`title mt-40 ${p.editMode}`}
				      onClick={() => p.editMode && p.SET_PAGE(`part_1.cards[${i}].title`, c.title)}>
					{c.title}
				</span>
				<span className={`caption mt-10 text-center mb-20 ${p.editMode}`}
				      onClick={() => p.editMode && p.SET_PAGE(`part_1.cards[${i}].caption`, c.caption)}>
					{c.caption}
				</span>
				<span className={`link centered pointer mt-auto px-12 py-6 ${p.editMode}`} onClick={() => {
					p.editMode ? p.SET_PAGE(`part_1.cards[${i}].link`, c.link) : this.setModalPayload(c.content);
				}}>
					{c.link}
				</span>
			</li>
		))
	}

	renderPart2List = () => {
		let {props: p} = this;

		return p.content().part_2.list.map((item, i) => (
			<li className={`item item-${i + 1} text-center mt-20 ${p.editMode}`} key={`item-${i + 1}`}
			    onClick={() => p.editMode && p.SET_PAGE(`part_2.cards[${i}]`, item)}>
				{item}
			</li>
		))
	}

	render() {
		let {props: p} = this;
		let {part_1: p1, part_2: p2} = this.props.content();

		return (
			<>
				<section id={'word-directions-section'} className="flex-column">
					<div className="part-1 centered py-100 px-15">
						<div className="content flex-column flex-align-center fade-up" ref={this.textRef1}>
							<span className={`title text-center ${p.editMode}`}
							      onClick={() => p.editMode && p.SET_PAGE(`part_1.title`, p1.title)}>
								{p1.title}
							</span>
							<div className="hr-line mt-20 w-50"/>
							<ul className="card-list flex-space-between mt-60">
								{this.renderPart1List()}
							</ul>
						</div>
					</div>

					<div className="part-2 centered px-15">
						<div className="content flex-column flex-align-center fade-up" ref={this.textRef2}>
							<span className={`title text-center ${p.editMode}`}
							      onClick={() => p.editMode && p.SET_PAGE(`part_2.title`, p2.title)}>
								{p2.title}
							</span>
							<ul className="list flex-column flex-align-center mt-10">
								{this.renderPart2List()}
							</ul>
						</div>
					</div>
				</section>
			</>
		);
	}
}


export default withUtils(connect(mapStateToProps, mapDispatchToProps)(WorkDirectionsSection));