/* eslint-disable */
import React, {Component} from 'react';
import { withUtils } from "./HOCs/withUtils";
import {connect} from "react-redux";
import {SET_PAGE} from "../actions/pageContentActions";

const mapStateToProps = s => {
	return {
		content: () => s.pageContent.aboutSection,
		editMode: s.pageContent.editMode ? 'edit_mode pointer' : ''
	}
};

const mapDispatchToProps = dispatch => {
	return {
		SET_PAGE: (keys, initValue) => dispatch(SET_PAGE('aboutSection', keys, initValue))
	};
};

class AboutSection extends Component {
	constructor(props) {
		super(props);
		this.textRef = React.createRef();
		this.animated_elements = [
			this.textRef
		];
	}

	componentDidMount() {
		this.props.setRefs(this.animated_elements);
	}

	render() {
		let {props: p} = this;
		let c = this.props.content();

		return (
			<section id={'about-section'} className="centered py-100 px-15">
				<div className="text flex-column flex-align-center fade-up" ref={this.textRef}>
					<span className={`title ${p.editMode}`}
					      onClick={() => p.editMode && p.SET_PAGE('title', c.title)}>
						{c.title}
					</span>
					<div className={'hr-line mt-20 w-50'} />
					<span className={`description text-center mt-20 ${p.editMode}`}
					      onClick={() => p.editMode && p.SET_PAGE('description', c.description)}>
						{c.description}
					</span>
				</div>
			</section>
		);
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(withUtils(AboutSection));